import React, { useState } from "react";
import {
  Box,
  Typography,
  Button,
  Paper,
  Grid,
  TextField,
  MenuItem,
  Divider,
} from "@mui/material";
import { title } from "framer-motion/client";

export const Admin = () => {
  const [formData, setFormData] = useState({
    title: "",
    description: "",
    type: "",
    priority: "",
    brand: "",
    model: "",
    year: "",
    discount: "",
    validUntil: "",
  });

  // NEW: control which section is visible
  const [activeSection, setActiveSection] = useState(null);

  const campaignTypes = ["Maintenance", "Promotion", "Seasonal"];
  const priorityLevels = ["Low", "Medium", "High"];

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(formData);
    alert("Campaign Created Successfully!");
  };

  return (
    <Box
      sx={{
        minHeight: "100vh",
        background: "linear-gradient(180deg, #000 0%, #111 100%)",
        color: "white",
        p: { xs: 2, sm: 4, md: 6 },
      }}
    >
      {/* =========================
          SECTION 1: Admin Dashboard + Navigation
          ========================= */}
      <Box sx={{ mb: 5 }}>
        <Typography variant="h4" fontWeight="bold" gutterBottom>
          Admin Dashboard
        </Typography>

        <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap" }}>
          {["Campaigns", "Bulk Email", "Notifications", "Newsletter"].map(
            (label) => (
              <Button
                key={label}
                variant="outlined"
                sx={{
                  color: "#00bcd4",
                  borderColor: "#00bcd4",
                  fontWeight: 500,
                  textTransform: "none",
                  "&:hover": {
                    backgroundColor: "#00bcd4",
                    color: "black",
                    borderColor: "#00bcd4",
                  },
                }}
                onClick={() => setActiveSection(label)} //  switch visible section
              >
                {label}
              </Button>
            )
          )}
        </Box>
      </Box>

      <Divider sx={{ borderColor: "rgba(255,255,255,0.2)", mb: 5 }} />

   {activeSection === "Bulk Email" && (
  <Box>
    <Typography variant="h5" fontWeight="bold" gutterBottom>
      Send Bulk Emails
    </Typography>
    <Typography sx={{ color: "rgba(255,255,255,0.7)", mb: 3 }}>
      Create and send Bulk Emails
    </Typography>

    <Paper
      sx={{
        p: { xs: 2, sm: 3, md: 4 },
        borderRadius: 3,
        background: "rgba(255,255,255,0.05)",
        border: "1px solid rgba(255,255,255,0.1)",
        mx: "auto",
        minHeight: "300px",
        height: "60vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        width: {
          xs: "95%", // full width on small screens
          sm: "90%",
          md: "85%",
          lg: "80%",
          xl: "70%", // smaller on large screens
        },
        maxWidth: "1500px",
      }}
    >
      <form onSubmit={handleSubmit} style={{ width: "100%" }}>
              <Grid
            container
            direction="column" //  forces vertical stacking
            spacing={3}
            sx={{ width: "100%" }}
          >
            {/* Subject Field */}
            <Grid item>
              <TextField
                fullWidth
                label="Subject"
                name="title"
                placeholder="Enter your message"
                value={formData.title}
                onChange={handleChange}
                InputLabelProps={{ style: { color: "#ccc" } }}
                InputProps={{
                  style: { color: "#ccc", height: "60px" },
                }}
                sx={{
                  "& .MuiOutlinedInput-root": {
                    height: "60px",
                  },
                  width: "100%",
                }}
                variant="outlined"
              />
            </Grid>

            {/* Message Field */}
            <Grid item>
              <TextField
                fullWidth
                multiline
                rows={13}
                label="Message"
                name="description"
                placeholder="Type your message here..."
                value={formData.description}
                onChange={handleChange}
                InputLabelProps={{ style: { color: "#ccc" } }}
                InputProps={{
                  style: { color: "#ccc" },
                }}
                sx={{
                  width: "100%",
                  "& .MuiOutlinedInput-root": {
                    alignItems: "flex-start",
                  },
                }}
                variant="outlined"
              />
            </Grid>


              {/* Dropdown Menu */}
            <Grid item>
              <TextField
                select
                fullWidth
                label="Target Audience"
                name="audience"
                value={formData.audience || ""}
                onChange={handleChange}
                InputLabelProps={{ style: { color: "#ccc" } }}
                InputProps={{ style: { color: "#ccc" } }}
                sx={{
                  width: "100%",
                  "& .MuiOutlinedInput-root": {
                    height: "60px",
                  },
                }}
                variant="outlined"
              >
                <MenuItem value="">Select Audience</MenuItem>
                <MenuItem value="all">All Customers</MenuItem>
                <MenuItem value="vip">Subscribed Customers</MenuItem>
                <MenuItem value="inactive">Inactive Customers</MenuItem>
              </TextField>
            </Grid>
</Grid>


      </form>
    </Paper>
  </Box>
)}


      {/*SECTION 2: Create Service Campaign Form*/}
      {activeSection === "Campaigns" && ( //  show only when "Campaigns" is active
        <Box>
          <Typography variant="h5" fontWeight="bold" gutterBottom>
            Create Service Campaign
          </Typography>
          <Typography sx={{ color: "rgba(255,255,255,0.7)", mb: 3 }}>
            Add new service campaigns and offers
          </Typography>

          <Paper
            sx={{
              p: 4,
              borderRadius: 3,
              background: "rgba(255,255,255,0.05)",
              border: "1px solid rgba(255,255,255,0.1)",
              maxWidth: 1500,
              mx: "auto",
              minHeight: "300px",
              height: "60vh",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            <form onSubmit={handleSubmit}>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Campaign Title"
                    name="title"
                    placeholder="e.g., Winter Tire Special"
                    value={formData.title}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    multiline
                    rows={1}
                    label="Description"
                    name="description"
                    placeholder="Campaign details..."
                    value={formData.description}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12} sm={6}>
                  <TextField
                    select
                    fullWidth
                    label="Type"
                    name="type"
                    value={formData.type}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  >
                    {campaignTypes.map((option) => (
                      <MenuItem key={option} value={option}>
                        {option}
                      </MenuItem>
                    ))}
                  </TextField>
                </Grid>

                <Grid item xs={12} sm={6}>
                  <TextField
                    select
                    fullWidth
                    label="Priority"
                    name="priority"
                    value={formData.priority}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  >
                    {priorityLevels.map((option) => (
                      <MenuItem key={option} value={option}>
                        {option}
                      </MenuItem>
                    ))}
                  </TextField>
                </Grid>

                <Grid item xs={12} sm={4}>
                  <TextField
                    fullWidth
                    label="Brand Filter"
                    name="brand"
                    placeholder="e.g., Toyota"
                    value={formData.brand}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12} sm={4}>
                  <TextField
                    fullWidth
                    label="Model Filter"
                    name="model"
                    placeholder="e.g., Camry"
                    value={formData.model}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12} sm={4}>
                  <TextField
                    fullWidth
                    label="Year Filter"
                    name="year"
                    placeholder="e.g., 2020"
                    value={formData.year}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Discount %"
                    name="discount"
                    placeholder="e.g., 20"
                    value={formData.discount}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Valid Until"
                    name="validUntil"
                    placeholder="dd/mm/yyyy"
                    value={formData.validUntil}
                    onChange={handleChange}
                    InputLabelProps={{ style: { color: "#ccc" } }}
                    InputProps={{ style: { color: "white" } }}
                    variant="outlined"
                  />
                </Grid>

                <Grid item xs={12}>
                  <Button
                    fullWidth
                    type="submit"
                    variant="contained"
                    sx={{
                      backgroundColor: "#00bcd4",
                      "&:hover": { backgroundColor: "#00acc1" },
                      color: "black",
                      fontWeight: "bold",
                      py: 1.5,
                    }}
                  >
                    Create Campaign
                  </Button>
                </Grid>
              </Grid>
            </form>
          </Paper>
        </Box>
      )}
    </Box>
  );
};
