import React, { useState } from "react";
import {
  Box,
  Button,
  Container,
  TextField,
  Typography,
  Paper,
  Divider,
  Snackbar,
  Alert,
} from "@mui/material";
import { motion } from "framer-motion";
import { Link, useNavigate } from "react-router-dom";
import Header from "./Header";
import { GoogleLogin } from "@react-oauth/google";
import {jwtDecode} from "jwt-decode";

export const AdminSignIn = () => {
  const navigate = useNavigate();
  const [snackbar, setSnackbar] = useState({ open: false, message: "", severity: "info" });

  const handleGoogleSuccess = async (credentialResponse) => {
    try {
      if (!credentialResponse || !credentialResponse.credential) return;

      const decoded = jwtDecode(credentialResponse.credential);

      const res = await fetch("http://localhost:3007/api/auth/google", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ id_token: credentialResponse.credential }),
      });

      const data = await res.json();

      if (data.success) {
        localStorage.setItem("userEmail", data.email);
        localStorage.setItem("userRole", data.role); // store role

        if (data.role === "admin") {
          navigate("/admin");
        } else {
          setSnackbar({
            open: true,
            message: "Sorry, you dont have admin access",
            severity: "error",
          });
        }
      } else {
        setSnackbar({
          open: true,
          message: data.error || "Google login failed",
          severity: "error",
        });
      }
    } catch (err) {
      console.error("Google login error:", err);
      setSnackbar({
        open: true,
        message: "Google login failed. Please try again.",
        severity: "error",
      });
    }
  };

  const handleGoogleError = () => {
    console.error("Google Login Failed");
    setSnackbar({
      open: true,
      message: "Google login failed. Please try again.",
      severity: "error",
    });
  };

  return (
    <>
      <Header />
      <Box
        sx={{
          background: "linear-gradient(180deg, #000 0%, #111 100%)",
          color: "white",
          minHeight: "100vh",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          py: 6,
        }}
      >
        <Container maxWidth="sm">
          <motion.div
            initial={{ opacity: 0, y: 40 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1 }}
          >
            <Paper
              elevation={6}
              sx={{
                p: 4,
                borderRadius: 3,
                background: "rgba(255,255,255,0.05)",
                backdropFilter: "blur(10px)",
              }}
            >
              <Typography
                variant="h4"
                textAlign="center"
                fontWeight="bold"
                gutterBottom
                sx={{
                  background: "linear-gradient(90deg, #fff, #00bcd4)",
                  WebkitBackgroundClip: "text",
                  WebkitTextFillColor: "transparent",
                }}
              >
                Admin
              </Typography>

              <Box
                component="form"
                sx={{ display: "flex", flexDirection: "column", gap: 2, mt: 3 }}
                onSubmit={(e) => e.preventDefault()}
              >
                <TextField
                  label="Email"
                  variant="outlined"
                  fullWidth
                  required
                  InputLabelProps={{ style: { color: "#aaa" } }}
                  InputProps={{ style: { color: "white" } }}
                />
                <TextField
                  label="Password"
                  type="password"
                  variant="outlined"
                  fullWidth
                  required
                  InputLabelProps={{ style: { color: "#aaa" } }}
                  InputProps={{ style: { color: "white" } }}
                />
                <Button
                  type="submit"
                  variant="contained"
                  fullWidth
                  sx={{
                    mt: 2,
                    py: 1.5,
                    fontWeight: 600,
                    backgroundColor: "#00bcd4",
                    "&:hover": { backgroundColor: "#00acc1" },
                  }}
                >
                  Sign In
                </Button>
              </Box>

              <Divider sx={{ my: 3, backgroundColor: "rgba(255,255,255,0.2)" }}>or</Divider>

              <Box sx={{ display: "flex", justifyContent: "center" }}>
                <GoogleLogin
                  onSuccess={handleGoogleSuccess}
                  onError={handleGoogleError}
                  useOneTap
                />
              </Box>

              <Typography
                variant="body2"
                textAlign="center"
                sx={{ mt: 3, color: "rgba(255,255,255,0.7)" }}
              >
                Dont have an account?{" "}
                <Link to="/signup" style={{ color: "#00bcd4" }}>
                  Sign Up
                </Link>
              </Typography>

              <Typography
                variant="body2"
                textAlign="center"
                sx={{ mt: 3, color: "rgba(255,255,255,0.7)" }}
              >
                Already have an Account?{" "}
                <Link to="/adminsignin" style={{ color: "#00bcd4" }}>
                  Log in
                </Link>
              </Typography>
            </Paper>
          </motion.div>
        </Container>
      </Box>

      {/* Snackbar for messages */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert severity={snackbar.severity} sx={{ width: "100%" }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default AdminSignIn;
